.experience-container {
  padding: 4rem 5%;
  background-color: #000000;
  max-width: 1400px;
  margin: 0 auto;
}

.experience-container h2 {
  font-size: 3rem;
  color: #1d68f2;
  margin-bottom: 3rem;
  text-align: center;
}

.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 2px;
  background: #1d68f2;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.timeline-item {
  padding: 10px 40px;
  position: relative;
  width: 50%;
  animation: slideIn 0.6s ease-out forwards;
  opacity: 0;
}

.timeline-item:nth-child(odd) {
  left: 0;
}

.timeline-item:nth-child(even) {
  left: 50%;
}

.timeline-content {
  padding: 2rem;
  background: rgba(29, 104, 242, 0.1);
  border-radius: 8px;
  position: relative;
  transition: all 0.3s ease;
}

.timeline-content:hover {
  transform: translateY(-5px);
  background: rgba(29, 104, 242, 0.15);
  box-shadow: 0 4px 15px rgba(29, 104, 242, 0.2);
}

.experience-header {
  margin-bottom: 1.5rem;
}

.experience-header h3 {
  color: var(--accent-color);
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
}

.company-name {
  color: #ffffff;
  font-size: 1.4rem;
  font-weight: 500;
}

.experience-details {
  display: flex;
  gap: 2rem;
  margin-bottom: 1.5rem;
  color: #bbbbbb;
}

.detail-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.detail-icon {
  color: var(--accent-color);
}

.experience-description {
  list-style: none;
  margin-bottom: 1.5rem;
  color: #ffffff;
}

.experience-description li {
  margin-bottom: 0.8rem;
  position: relative;
  padding-left: 1.5rem;
}

.experience-description li::before {
  content: '•';
  color: var(--accent-color);
  position: absolute;
  left: 0;
  font-weight: bold;
}

.skills-used {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
}

.skill-tag {
  background: rgba(29, 104, 242, 0.2);
  color: #ffffff;
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.skill-tag:hover {
  background: var(--accent-color);
  transform: translateY(-2px);
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .timeline::after {
    left: 31px;
  }

  .timeline-item {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

  .timeline-item:nth-child(even) {
    left: 0;
  }
}

@media (max-width: 768px) {
  .experience-container {
    padding: 2rem 5%;
  }

  .experience-header h3 {
    font-size: 1.5rem;
  }

  .company-name {
    font-size: 1.2rem;
  }

  .experience-details {
    flex-direction: column;
    gap: 1rem;
  }
}
