.featured-projects {
  background-color: #000000;
  color: #1d68f2;
  padding: clamp(3rem, 5vw, 5%) clamp(5%, 10%, 10%);
  text-align: center;
  width: 100%;
  font-family: 'Inter', sans-serif;
}

.featured-projects h1 {
  font-size: clamp(2.5rem, 4vw, 4rem);
  font-weight: 700;
  color: #1d68f2;
  margin-bottom: clamp(2rem, 2.5vw, 2.5rem);
  font-family: 'Inter', sans-serif;
}

.projects-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
}

.project-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: #1d68f2;
  padding: clamp(2rem, 3vw, 3rem);
  border-radius: clamp(10px, 1vw, 15px);
  box-shadow: 0 6px 15px rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease;
  gap: clamp(2rem, 3vw, 3rem);
  font-family: 'Inter', sans-serif;
}

.project-card:nth-child(even) {
  flex-direction: row-reverse;
}

.project-info {
  position: relative;
  z-index: 10;
  width: 60%;
  text-align: left;
  transition: transform 0.3s ease;
}

.project-info h2 {
  font-size: clamp(1.8rem, 2.5vw, 2.5rem);
  color: white;
  font-weight: 700;
  margin-bottom: clamp(0.8rem, 1vw, 1rem);
  font-family: 'Inter', sans-serif;
}

.project-info p {
  font-size: clamp(1rem, 1.2vw, 1.2rem);
  color: white;
  margin: 1.5rem 0;
  line-height: 1.6;
  font-family: 'Inter', sans-serif;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin: 1.5rem 0;
}

.tech {
  background-color: #e4e6e9;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  font-size: 1rem;
  color: #2b2d42;
  font-weight: 600;
  font-family: 'Inter', sans-serif;
}

.project-links {
  margin-top: 1.5rem;
}

.project-link {
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
  display: inline-block;
  margin-right: 1rem;
}

.project-logo {
  width: 30px;
  height: 30px;
}

.project-card:hover .project-info {
  transform: translateX(-10px);
}

.project-card:nth-child(even):hover .project-info {
  transform: translateX(10px);
}

.project-image-container {
  position: relative;
  width: 40%;
  height: 100%;
  overflow: hidden;
}

.project-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 15px;
  transition: transform 0.3s ease-in-out;
}

.project-card:hover .project-image {
  transform: scale(1);
}

.project-card:hover .project-info {
  color: #e4e6e9;
}

.project-link:hover {
  color: #0056b3;
}

.project-link:focus {
  outline: none;
}

@media (max-width: 768px) {
  .project-card,
  .project-card:nth-child(even) {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    padding: 2rem;
  }

  .project-info {
    width: 100%;
    text-align: center;
  }

  .project-image-container {
    width: 100%;
  }

  .project-image {
    transform: scale(1);
  }
}

.theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 10px;
  border-radius: 50%;
  border: none;
  background: transparent;
  cursor: pointer;
  color: inherit;
  transition: transform 0.3s;
}

.theme-toggle:hover {
  transform: scale(1.1);
}
