/* Main container for the About Me section */
.about-me {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 90vh;
  padding: clamp(2rem, 4vw, 4rem) clamp(3%, 5%, 5%);
  background: #000000; /* Dark green background */
  color: #bbbbbb; /* Default text color */
  font-family: 'Roboto', sans-serif; /* Consistent modern font */
  flex-wrap: wrap; /* Allow content to wrap on smaller screens */
  gap: clamp(2rem, 3vw, 3rem);
}

/* Left side with profile image */
.left-side {
  flex: 1;
  display: flex;
  flex-direction: column; /* Stack image and caption vertically */
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem; /* Space below image on smaller screens */
}

.profile-pic {
  width: clamp(250px, 30vw, 500px);
  height: auto;
  border: 5px solid #bbbbbb; /* White border for a clean look */
  object-fit: cover; /* Ensures image is well-cropped */
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.4); /* Deeper shadow for modern feel */
}

/* Styling for the image caption */
.image-caption {
  text-align: center;
  font-size: 1.2rem;
  color: #d0d0d0; /* Softer light gray */
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  margin-top: 1rem; /* Adjust spacing from the image */
  letter-spacing: 0.5px;
}

/* Right side with description */
.right-side {
  flex: 1;
  padding-left: 2rem;
  text-align: left;
}

/* Title styling */
.right-side h2 {
  font-size: clamp(2rem, 3vw, 3rem);
  color: #1d68f2; /* Accent color */
  text-shadow: 3px 3px 8px rgba(88, 7, 7, 0.7); /* Stronger text shadow for emphasis */
  font-family: 'Roboto', sans-serif; /* Clean and modern font */
  font-weight: 700; /* Bold for emphasis */
  margin: 0;
  margin-bottom: clamp(1.5rem, 2vw, 2rem);
  text-transform: none; /* No uppercase */
  letter-spacing: 2px; /* Added spacing for elegance */
  border: none;
}

/* Body text styling */
.right-side p {
  font-size: clamp(1rem, 1.2vw, 1.4rem);
  margin-bottom: clamp(1rem, 1.5vw, 1.5rem);
  line-height: 1.2;
  color: #d0d0d0; /* Softer light gray instead of pure white */
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  letter-spacing: 0.5px;
}

/* Link Styling */
.right-side a {
  color: #1da1f2; /* Accent color */
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  transition: color 0.3s ease;
}

.right-side a:hover {
  text-decoration: underline;
  color: #00aced; /* Lighter blue on hover */
}

/* Read More Button Styling */
.read-more-btn {
  background-color: #1d68f2; /* Button color */
  color: #ffffff; /* Text color */
  font-size: 1.5rem;
  padding: 1.2rem 2.5rem; /* Slightly larger button for prominence */
  border: none;
  border-radius: 50px;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
  font-weight: 600; /* Bolder text for emphasis */
  transition: all 0.3s ease;
  margin-top: 2rem; /* More space above button */
  text-align: center;
  display: inline-block;
  box-shadow: 0 10px 20px rgba(230, 0, 0, 0.2); /* Strong shadow for depth */
}

.read-more-btn:hover {
  background-color: #00aced;
  transform: translateY(-3px); /* Slight lift effect */
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.3); /* More pronounced shadow on hover */
}

.read-more-btn:focus {
  outline: none;
  border: 2px solid #ffffff; /* White border on focus */
}

.read-more-btn:active {
  background-color: #0088cc; /* Darker shade when clicked */
  transform: translateY(2px); /* Push effect when clicked */
}

/* Optional: Global styles for consistency across pages */
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  color: #ffffff; /* Default text color */
  background: #2b2d42; /* Consistent dark background */
}

/* Media Queries for responsiveness */
@media (max-width: 1200px) {
  .right-side h2 {
    font-size: 3.2rem; /* Adjust for medium screens */
  }

  .right-side p {
    font-size: 1.5rem; /* Adjust text size for medium screens */
  }

  .left-side {
    margin-bottom: 1rem;
  }
}

@media (max-width: 1100px) {
  /* Stack elements vertically on smaller screens */
  .about-me {
    flex-direction: column;
    text-align: center;
    height: auto;
    padding: 2rem 5%;
  }

  .left-side {
    margin-bottom: 2rem;
  }

  .profile-pic {
    width: 80%; /* Scale image down */
    max-width: 300px;
  }

  .right-side {
    padding-left: 0;
  }

  .right-side h2 {
    font-size: 2.8rem;
  }

  .right-side p {
    font-size: 1.3rem;
  }
}

@media (max-width: 480px) {
  /* Adjust for extra small screens */
  .right-side h2 {
    font-size: 2.2rem;
  }

  .right-side p {
    font-size: 1.1rem;
  }
}

.about-me-detailed {
  min-height: 100vh;
  padding: 5% 10%;
  background: #000000;
  color: #bbbbbb;
}

.about-me-detailed h1 {
  font-size: 4rem;
  color: #1d68f2;
  margin-bottom: 2rem;
  text-align: center;
}

.detailed-content {
  max-width: 800px;
  margin: 0 auto;
}

.detailed-content p {
  font-size: 1.6rem;
  line-height: 1.6;
  margin-bottom: 2rem;
}

.detailed-content ul {
  list-style-type: disc;
  margin-left: 2rem;
  margin-top: 1rem;
}

.detailed-content li {
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
  color: #d0d0d0;
}

.back-button {
  display: inline-block;
  padding: 10px 20px;
  margin: 20px;
  background-color: #1d68f2;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #1550c8;
}

/* Timeline styling */
.timeline {
  position: relative;
  max-width: 800px;
  margin: 40px auto;
  padding: 40px 0;
}

/* Vertical line */
.timeline::after {
  content: '';
  position: absolute;
  width: 4px;
  background-color: #1d68f2;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -2px;
  border-radius: 2px;
  box-shadow: 0 0 10px rgba(29, 104, 242, 0.5);
}

/* Timeline items */
.timeline-item {
  padding: 10px 40px;
  position: relative;
  width: 50%;
  animation: fadeIn 0.5s ease-in-out;
}

/* Animation for timeline items */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Alternating items */
.timeline-item:nth-child(odd) {
  left: 0;
}

.timeline-item:nth-child(even) {
  left: 50%;
}

/* Date circles */
.timeline-date {
  background: #1d68f2;
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  font-weight: bold;
  display: inline-block;
  margin-bottom: 15px;
  box-shadow: 0 0 10px rgba(29, 104, 242, 0.3);
}

/* Content boxes */
.timeline-content {
  padding: 20px;
  background: #1c1c1c;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}

.timeline-content:hover {
  transform: translateY(-5px);
}

.timeline-content h3 {
  color: #1d68f2;
  margin-bottom: 10px;
  font-size: 1.8rem;
}

.timeline-content p {
  color: #d0d0d0;
  line-height: 1.6;
  font-size: 1.2rem;
}

/* Dots on the timeline */
.timeline-item::before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: #1d68f2;
  border: 4px solid #000000;
  border-radius: 50%;
  top: 15px;
  right: -10px;
  z-index: 1;
  box-shadow: 0 0 10px rgba(29, 104, 242, 0.5);
}

.timeline-item:nth-child(even)::before {
  left: -10px;
}

/* Responsive design */
@media screen and (max-width: 768px) {
  .timeline::after {
    left: 31px;
  }
  
  .timeline-item {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  
  .timeline-item:nth-child(even) {
    left: 0;
  }
  
  .timeline-item::before {
    left: 21px;
    right: auto;
  }
}

/* Timeline icon styling */
.timeline-icon {
  margin-left: 8px;
  font-size: 1.2em;
  vertical-align: middle;
}

.timeline-date {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  min-width: 120px;
}

/* Update existing responsive styles */
@media screen and (max-width: 1200px) {
  .timeline {
    margin: 20px auto;
    padding: 20px;
  }

  .timeline-content h3 {
    font-size: 1.6rem;
  }

  .timeline-content p {
    font-size: 1.1rem;
  }
}

@media screen and (max-width: 1024px) {
  .about-me {
    padding: 2rem 3%;
  }

  .right-side h2 {
    font-size: 2.8rem;
  }

  .right-side p {
    font-size: 1.4rem;
  }

  .profile-pic {
    max-width: 500px;
  }
}

@media screen and (max-width: 768px) {
  /* Timeline mobile adjustments */
  .timeline::after {
    left: 31px;
  }
  
  .timeline-item {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  
  .timeline-item:nth-child(even) {
    left: 0;
  }
  
  .timeline-item::before {
    left: 21px;
    right: auto;
  }

  .timeline-content {
    padding: 15px;
  }

