    font-size: 0.9rem;
    padding: 8px 16px;
  }

  /* About me mobile adjustments */
  .about-me {
    flex-direction: column;
    height: auto;
    padding: 2rem 5%;
  }

  .left-side {
    margin-bottom: 2rem;
  }

  .profile-pic {
    width: 80%;
    max-width: 300px;
  }

  .right-side {
    padding-left: 0;
    text-align: center;
  }

  .right-side h2 {
    font-size: 2.5rem;
  }

  .right-side p {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 480px) {
  .timeline-content h3 {
    font-size: 1.4rem;
  }

  .timeline-content p {
    font-size: 1rem;
  }

  .timeline-date {
    font-size: 0.8rem;
    padding: 6px 12px;
  }

  .right-side h2 {
    font-size: 2rem;
  }

  .right-side p {
    font-size: 1rem;
  }

  .read-more-btn {
    font-size: 1.2rem;
    padding: 1rem 2rem;
  }
}

/* Add smooth transitions for all elements */
* {
  transition: all 0.3s ease-in-out;
}

/* Specific transitions for important elements */
.about-me,
.timeline-item,
.timeline-content,
.profile-pic,
.right-side,
.read-more-btn {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Prevent transition on page load */
.no-transition {
  transition: none !important;
}
