/* src/Skills.css */

/* General styling for the section */
.skills {
  background-color: #000000; /* Light background for consistency */
  padding: 5% 10%;
  border-radius: 15px;
  margin-top: 3rem;
  width: 100%;
  max-width: 100%; /* Ensure full width */
  text-align: left;
  margin-left: 0; /* Ensure full-width */
  margin-right: 0; /* Ensure full-width */
  font-family: 'Inter', sans-serif; /* Consistent font */
}

/* Heading style */
.skills h2 {
  font-size: 4rem;
  color: #1d68f2; /* Dark text color */
  font-weight: 700;
  margin-bottom: 2rem; /* Adjust this margin if needed */
  text-align: center;
  font-family: 'Roboto', sans-serif; /* Consistent font for headings */
  border: none; /* Ensure there's no border */
  padding-bottom: 0; /* Remove any padding that might cause the line */
}


/* Flexbox for the skills section */
.skills-section {
  display: flex;
  flex-wrap: wrap; /* Allow categories to wrap */
  gap: 2rem; /* Space between categories */
  justify-content: space-between; /* Distribute categories across screen */
}

.skills-category {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 30%; /* Each category takes 30% of screen */
}

.skills-category h3 {
  font-size: 1.8rem;
  color: #1d68f2;
  margin-bottom: 1rem;
  font-weight: 600;
  width: 100%;
  text-align: left;
}

.skills-category ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start; /* Align skills to the left */
  gap: 1rem;
}

.skills-category li {
  font-size: 1.1rem;
  color: #2b2d42;
  padding: 0.8rem 1.2rem;
  border: 2px solid #2b2d42;
  border-radius: 25px;
  background-color: #ffffff;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  text-align: center;
  font-weight: 500;
}

.skills-category li:hover {
  background-color: #2b2d42;
  color: #ffffff;
}

/* Link styling inside list items */
.skills-category li a {
  text-decoration: none;
  color: #2b2d42;
  font-weight: 600;
  display: block;
  padding: 5px 10px;
}

.skills-category li:hover a {
  color: #ffffff;
}

/* Media Queries for responsiveness */
@media (max-width: 1024px) {
  .skills-category {
    width: 45%; /* Adjust category width for medium screens */
  }
}

@media (max-width: 768px) {
  .skills-category {
    width: 100%; /* Stack categories on small screens */
  }
}

.skills-container {
  padding: clamp(2rem, 4vw, 4rem) clamp(3%, 5%, 5%);
  max-width: var(--container-max);
  margin: 0 auto;
}

.skills-container h2 {
  font-size: clamp(2rem, 3vw, 3rem);
  margin-bottom: clamp(2rem, 3vw, 3rem);
}

.skills-category {
  margin-bottom: 4rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skills-category h3 {
  font-size: clamp(1.5rem, 2vw, 2rem);
  color: #00b4d8;
  margin-bottom: clamp(1.5rem, 2vw, 2rem);
  border-bottom: 2px solid #1d68f2;
  padding-bottom: 0.5rem;
  text-align: center;
  width: 90%;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(clamp(120px, 15vw, 160px), 1fr));
  gap: clamp(1rem, 2vw, 2.5rem);
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: clamp(1rem, 1.5vw, 1.5rem);
  background: rgba(29, 104, 242, 0.1);
  border-radius: 8px;
  transition: all 0.3s ease;
  width: clamp(120px, 15vw, 160px);
  height: clamp(120px, 15vw, 160px);
}

.skill-item:hover {
  transform: translateY(-5px);
  background: rgba(29, 104, 242, 0.15);
  box-shadow: 0 4px 15px rgba(29, 104, 242, 0.2);
}

.skill-icon {
  font-size: clamp(2rem, 2.5vw, 2.5rem);
  margin-bottom: 1rem;
  color: #1d68f2;
  transition: all 0.3s ease;
}

.skill-item:hover .skill-icon {
  color: #00b4d8;
  transform: scale(1.1);
}

.skill-name {
  font-size: 1rem;
  font-weight: 500;
  color: #ffffff;
}

/* Media queries updated for better centering */
@media (max-width: 1400px) {
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 150px));
    gap: 2rem;
  }
  
  .skill-item {
    width: 150px;
    height: 150px;
  }
}

@media (max-width: 1024px) {
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(140px, 140px));
    gap: 2rem;
  }
  
  .skill-item {
    width: 140px;
    height: 140px;
  }
}

@media (max-width: 768px) {
  .skills-container {
    padding: 2rem 5%;
  }

  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(130px, 130px));
    gap: 1.5rem;
  }

  .skill-item {
    width: 130px;
    height: 130px;
  }
}

@media (max-width: 480px) {
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 120px));
    gap: 1rem;
  }

  .skill-item {
    width: 120px;
    height: 120px;
  }
}

/* Ensure smooth transitions */
* {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
